<template>
  <div class="lb" v-if="items.length>0">
    <div class="lb-grid" :class="[css,items.length>cells?'lb-grid-' + cells: 'lb-grid-' + items.length]">
      <a class="lb-item" :key="src" v-for="(src, i) in items" :href="items[i]" role="link" @click.prevent="show(i)"  :style="bg(src)">
        <span class="lb-more" v-if="i==cells-1 && items.length - cells>0">{{ items.length - cells}}+</span>
      </a>
    </div>
    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
      <div class="lb-modal" v-if="index>=0">
        <i-button size="sm" variant="danger" outline class="lb-modal-close" @click="close">
          <i class='bx bx-x' ></i>
        </i-button>
        <i-button size="lg"  outline class="lb-modal-prev" @click="prev">
          <i class='bx bx-chevron-left-circle' ></i>

        </i-button>
        <i-button  size="lg"  outline class="lb-modal-next" @click="next">
          <i class='bx bx-chevron-right-circle'></i>
        </i-button>

        <div class="lb-modal-img" @click="close">
          <img :src="src">
          <div class="spinner spinner-dots-wave" v-if="loading">
                      <i-loader></i-loader>
          </div>
        </div>
        <p class="_text-white _margin-left-3">{{index==0?1:items.length-index+1}}/{{items.length}}</p>
      </div>

    </transition>

  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      default: () => { return [] }
    },
    css: {
      type: String,
      default: () => 'h-250 h-md-400 h-lg-600'
    },
    cells: {
      type: Number,
      default: () => 5
    }
  },
  data() {
    return {
      src: '',
      index: -1,
      loading: false,
      events: []
    }
  },
  methods: {
    bind() {
      if (this.events.length > 0) return;
      this.events.push(['keydown', e => {
        if (this.index < 0) return;
        if (e.keyCode === 37) {
          this.prev();
        } else if (e.keyCode === 39) {
          this.next();
        } else if (e.keyCode === 27) {
          this.close();
        }
      }]);
      this.events.forEach(e => { window.addEventListener(e[0], e[1]); });
    },
    show(i) {
      if (i >= this.items.length) {
        i = 0;
      }
      if (i < 0) {
        i = this.items.length - 1;
      }
      this.loading = true;
      this.bind();
      this.index = i;
      var src = this.items[i];
      var img = new Image();
      img.src = src;
      img.onload = () => {
        this.loading = false;
        this.src = src;
      }
    },
    next() {
      this.show(this.index - 1);
    },
    prev() {
      this.show(this.index + 1);
    },
    close() {
      this.index = -1;
      this.events.forEach(e => { window.removeEventListener(e[0], e[1]); });
      this.events = [];
    },
    bg(i) {
      return i && i.length > 0 ? `background-image: url('${i}')` : '';
    }
  },
}
</script>
